import React, { Component } from 'react'
import "../CSS/Kontakt.css"
import classnames from 'classnames'

export default class KontaktInput extends Component {
 constructor(){
   super()
   this.state = {
      name: "",
      eMail: "",
      tel: "",
      msg: ""
   }
   this.handleChange = this.handleChange.bind(this)
 }
 handleChange(event){
   const {name, value} = event.target
   this.setState({
     [name]: value
   })
 }
 
  render() {
    return (
      
         <div className={classnames(this.props.kontakt ? "slideRight" : "", "hideLeft")}>
       <div id="inputInfo" className="">
        <p className="text-center pTxt">Lub napisz tutaj:</p>
        <form className="d-flex flex-column justify-content-center align-items-center">
        <input type="text"  
        value={this.state.name}
        name="name"
        placeholder="Twoje imię i nazwisko" 
        className="mt-1 inputBox"
        onChange={this.handleChange}/>
        <input type="email"
        value={this.state.eMail}
        name="eMail"  
        placeholder="Twój e-mail" 
        className="mt-1 inputBox"
        onChange={this.handleChange}/>
        <input type="tel"
        value={this.state.tel}
        name="tel"
         placeholder="Twój telefon" 
        className="mt-1 inputBox" 
        onChange={this.handleChange}/>
        <textarea type="text"
        value={this.state.msg}
        name="msg" 
        placeholder="Twoja wiadomość" 
        className="mt-1 inputBox"
        onChange={this.handleChange}/>
        <button type="submit" className="btn btn-danger mt-1 col-1 btnSub">Submit</button>
        </form>
      </div>
    </div>
      
    )
  }
}
