import React, { Component } from 'react'
import './CSS/Kontakt.css'
import KontaktInfo from "./Kontakt/KontaktInfo"
import KontaktInput from "./Kontakt/KontaktInput"
import { Waypoint } from 'react-waypoint';
import classnames from 'classnames'

export default class Kontakt extends Component {
  constructor(){
    super()
    this.state = {
      kontakt: false

    }
    this.onEnter1 = this.onEnter1.bind(this)
  }

  onEnter1(){
    this.setState({
      kontakt: true
    })
  }
  
  
  render() {
    return (
      <div id="kontaktDiv" className="container-fluid">
       <h1 className={classnames(this.state.kontakt ? "slideRight" : "", "kntTxt hideLeft")}>Kontakt</h1>
       <Waypoint onEnter={this.onEnter1} />
      <KontaktInfo kontakt={this.state.kontakt} />
      <KontaktInput kontakt={this.state.kontakt} />
     
      </div>
    )
  }
}
