import React, { Component } from 'react'
import { Waypoint } from 'react-waypoint';
import './CSS/Navbar.css'
import classnames from 'classnames'

export default class Navbar extends Component {
  constructor(){
    super()
    this.state = {
         isVisible: true,
         burgerMenu: false
    }
    this.onLeave = this.onLeave.bind(this)
    this.onEnter = this.onEnter.bind(this)
    this.handleClick = this.handleClick.bind(this)
  }

 onLeave(){
    this.setState({isVisible: false})
  }
 onEnter(){
  this.setState({isVisible: true})
 }
 handleClick(){
  this.setState(prevState =>  {
    return {
      burgerMenu: !prevState.burgerMenu
    }
  })
 }
 
  render() {
    return (
      <div>
      <Waypoint onEnter={this.onEnter} />
      <div className={this.state.isVisible ? "navRelative" : "navFixed" }>
      <nav id="mainNav" className="d-flex justify-content-end align-items-center" >
        <h4 className="ml-5 mr-5"><a href="#header" className="link">Start</a></h4>
        <h4 className="ml-5 mr-5"><a href="#poznajmy" className="link">Poznajmy się!</a></h4>
        <h4 className="ml-5 mr-5"><a href="#projects" className="link">Projekty</a></h4>
        <h4 className="ml-5 mr-5"><a href="#kontaktDiv" className="link">Kontakt</a></h4>
        </nav>
        
    </div>
    <Waypoint
        onLeave={this.onLeave} />
        <nav id="burgerNav" className={classnames(this.state.isVisible ? "burgerHidden" : "burgerFixed", "burgerNav" ) }>
        <i id="burger" className="fas fa-bars" onClick={this.handleClick}></i>
        <ul className={classnames(this.state.burgerMenu ? "menuClose menuOpen" : "menuClose", "burgerNav menuFlex") }>
          <li><a href="#header" className="linkBrg">Start</a></li>
          <li><a href="#poznajmy" className="linkBrg">Poznajmy się!</a></li>
          <li><a href="#projects" className="linkBrg">Projekty</a></li>
          <li><a href="#kontaktDiv" className="linkBrg">Kontakt</a></li>
        </ul>
        </nav>
      </div>
    )
  }
}
