import React, { Component } from 'react'
import './CSS/Main.css'
import malpie from "../Imgs/malpie.jpg"
import { Waypoint } from 'react-waypoint';
import classnames from 'classnames'

export default class Main extends Component {
  constructor(){
    super()
    this.state = {
      poznajmy: false

    }
    this.onEnter1 = this.onEnter1.bind(this)
  }

  onEnter1(){
    this.setState({
      poznajmy: true
    })
  }

  render() {
    return (
      <main className="container-fluid">
        <h1 id="poznajmy" className={classnames(this.state.poznajmy ? "slideRight" : "","text-center hideLeft")}>Poznajmy się!</h1>
        <Waypoint onEnter={this.onEnter1} />
        <div id="general-info" className="d-flex align-items-start justify-content-center">
        <div id="opis" className={classnames(this.state.poznajmy ? "slideLeft" : "","hideRight")}>
        <img src={malpie} alt="Img" className="myImg"/>
        <p className="text-center">Jestem frond end developerem. Robię strony za pomocą technologi React.js. Dzięki temu są one szybkie, ineraktywne i funkcjonalne. Jeśli jesteś zaintersowany/a współpracą kliknij tutaj, aby pobrać moje CV!</p>
        </div>
        <div id="technologie" className={classnames(this.state.poznajmy ? "slideRight" : "", "d-flex flex-column justify-content-center align-items-center hideLeft")}>
        <h4 className=" text-center"> Technologie jakich używam do tworzenia stron:</h4>
        <i class="fab fa-react icon"></i>
        <p className="text-danger font-italic">React</p>
        <i class="fab fa-js-square icon"></i>
        <p className="text-danger font-italic">Javascript i JQuery</p>
        <i class="fab fa-bootstrap icon"></i>
        <p className="text-danger font-italic">Bootstrap</p>
        <i class="fab fa-sass icon"></i>
        <p className="text-danger font-italic">Sass</p>

        </div>
        </div>
        
      </main>
    )
  }
}
