import React, { Component } from 'react'
import './CSS/Projects.css'
import projectsData from "./Projects/projectsData"
import ProjectDivs from "./Projects/ProjectDivs"
import { Waypoint } from 'react-waypoint';
import classnames from 'classnames'

export default class Projects extends Component {
constructor(){
  super()
  this.state = {
    project: projectsData,
    projekty: false
  }
  this.onEnter1 = this.onEnter1.bind(this)
}
 
onEnter1(){
  this.setState({
    projekty: true
  })
}
 

  render() {
  const projectList = this.state.project.map(item => <ProjectDivs key={item.id} item={item} projekty={this.state.projekty} />)
    return (
      <section id="projects" style={{marginTop: "100px"}}>
        <h1 className={classnames(this.state.projekty ? "slideRight" : "","text-center hideLeft")}>Projekty</h1>
        <Waypoint onEnter={this.onEnter1} />
<div id="projectGrid" className="row" style={{marginTop: "100px"}}>
{projectList}
</div>

      </section>
    )
  }
}
